import * as React from 'react'
import { LiveProvider, LiveEditor, LiveError, LivePreview } from 'react-live'
import { a, useSpring } from 'react-spring'

const ReactLiveProvider = ({ code, type }) => {
  let scope
  let transformCode
  if (type == 'true') {
    scope = {
      Test: ({ props }) => {
        const style = useSpring(props)
        return (
          <a.div
            style={{
              width: 100,
              height: 100,
              backgroundColor: 'pink',
              borderRadius: 12,
              ...style,
            }}
          />
        )
      },
    }
    transformCode = code => `
      <Test props={${code}} />
    `
  }
  return (
    <LiveProvider code={code} scope={scope} transformCode={transformCode}>
      <LiveEditor />
      <LiveError />
      <LivePreview />
    </LiveProvider>
  )
}

export default ReactLiveProvider
